<template>
    <Layout style="height: 100%" class="main">
        <Sider hide-trigger collapsible :width="250" :collapsed-width="64" v-model="collapsed">
            <side-menu accordion :active-name="$route.name" :collapsed="collapsed" @on-select="turnToPage"
                       :menu-list="menuList">
                <!-- Logoside-menu -->
                <div class="logo-con">
                    <img v-show="!collapsed" :src="maxLogo" key="max-logo"/>
                    <img v-show="collapsed" :src="minLogo" key="min-logo"/>
                </div>
            </side-menu>
        </Sider>
        <Layout>
            <Header class="header-con">
                <header-bar :collapsed="collapsed" @on-coll-change="handleCollapsedChange">
                    <user :user-avator="userAvator"/>
                </header-bar>
            </Header>
            <Content>
                <Layout>
                    <!--<div class="tag-nav-wrapper">
                        <tags-nav :value="$route" @input="handleClick" :list="tagNavList" @on-close="handleCloseTag"/>
                    </div>-->
                    <Content class="content-wrapper">
                        <router-view/>
                        <!--<keep-alive :include="cacheList">
                            <router-view/>
                        </keep-alive>-->
                    </Content>
                </Layout>
            </Content>
        </Layout>
    </Layout>
</template>
<script>
    import SideMenu from './components/side-menu';
    import HeaderBar from './components/header-bar';
    import TagsNav from './components/tags-nav';
    import User from './components/user';
    import {mapMutations, mapActions} from 'vuex';
    import {getNewTagList, getNextName} from '@/libs/util';
    import minLogo from '@/assets/images/logo-min.png';
    import maxLogo from '@/assets/images/logo2.png';
    import './main.less';

    export default {
        name: 'Main',
        components: {
            SideMenu,
            HeaderBar,
            TagsNav,
            User
        },
        data () {
            return {
                collapsed: false,
                minLogo,
                maxLogo
            };
        },
        computed: {
            tagNavList () {
                return this.$store.state.app.tagNavList;
            },
            tagRouter () {
                return this.$store.state.app.tagRouter;
            },
            userAvator () {
                return this.$store.state.user.avatorImgPath;
            },
            cacheList () {
                return this.tagNavList.length ? this.tagNavList.filter(item => !(item.meta && item.meta.notCache)).map(item => item.name) : [];
            },
            menuList () {
                return this.$store.getters.menuList;
            }
        },
        methods: {
            ...mapMutations([
                'setBreadCrumb',
                'setTagNavList',
                'addTag'
            ]),
            ...mapActions([
                'handleLogin'
            ]),
            turnToPage (name) {
                this.$router.push({
                    name: name
                });
            },
            handleCollapsedChange (state) {
                this.collapsed = state;
            },
            handleCloseTag (res, type, name) {
                const nextName = getNextName(this.tagNavList, name);
                this.setTagNavList(res);
                if (type === 'all') {
                    this.turnToPage('home');
                } else if (this.$route.name === name) this.$router.push({name: nextName});
            },
            handleClick (item) {
                this.turnToPage(item.name);
            }
        },
        watch: {
            '$route' (newRoute) {
                this.setBreadCrumb(newRoute.matched);
                this.setTagNavList(getNewTagList(this.tagNavList, newRoute));
            }
        },
        mounted () {
            /**
             * @description 
             */
            this.setTagNavList();
            this.addTag(this.$store.state.app.homeRoute);
            this.setBreadCrumb(this.$route.matched);
        }
    }
</script>
