import Main from '@/view/main';
import parentView from '@/components/parent-view';

export default [
    {
        path: '/login',
        name: 'login',
        meta: {
            title: 'Login - ',
            hideInMenu: true
        },
        component: () => import('@/view/login/login.vue')
    },
    {
        path: '/',
        name: 'home',
        redirect: '/home',
        component: Main,
        meta: {
            hideInMenu: true,
            notCache: true
        },
        children: [
            {
                path: 'home',
                name: 'home',
                meta: {
                    hideInMenu: true,
                    notCache: true
                },
                component: () => import('@/view/single-page/home')
            }
        ]
    },
    {
        path: '/components',
        name: 'components',
        meta: {
            icon: 'social-buffer',
            title: ''
        },
        component: Main,
        children: [
            {
                path: 'count_to_page',
                name: 'count_to_page',
                meta: {
                    icon: 'arrow-graph-up-right',
                    title: ''
                },
                component: () => import('@/view/components/count-to/count-to.vue')
            },
            {
                path: 'tables_page',
                name: 'tables_page',
                meta: {
                    icon: 'ios-grid-view',
                    title: ''
                },
                component: () => import('@/view/components/tables/tables.vue')
            },
            {
                path: 'split_pane_page',
                name: 'split_pane_page',
                meta: {
                    icon: 'pause',
                    title: ''
                },
                component: () => import('@/view/components/split-pane/split-pane.vue')
            },
            {
                path: 'markdown_page',
                name: 'markdown_page',
                meta: {
                    icon: 'social-markdown',
                    title: 'Markdown'
                },
                component: () => import('@/view/components/markdown/markdown.vue')
            },
            {
                path: 'editor_page',
                name: 'editor_page',
                meta: {
                    icon: 'compose',
                    title: ''
                },
                component: () => import('@/view/components/editor/editor.vue')
            },
            {
                path: 'icons_page',
                name: 'icons_page',
                meta: {
                    icon: '_bear',
                    title: ''
                },
                component: () => import('@/view/components/icons/icons.vue')
            }
        ]
    },
    {
        path: '/update',
        name: 'update',
        meta: {
            icon: 'upload',
            title: ''
        },
        component: Main,
        children: [
            {
                path: 'update_table_page',
                name: 'update_table_page',
                meta: {
                    icon: 'document-text',
                    title: 'Csv'
                },
                component: () => import('@/view/update/update-table.vue')
            },
            {
                path: 'update_paste_page',
                name: 'update_paste_page',
                meta: {
                    icon: 'clipboard',
                    title: ''
                },
                component: () => import('@/view/update/update-paste.vue')
            }
        ]
    },
    {
        path: '/directive',
        name: 'directive',
        meta: {
            hide: true
        },
        component: Main,
        children: [
            {
                path: '/directive_page',
                name: 'directive_page',
                meta: {
                    icon: 'ios-navigate',
                    title: ''
                },
                component: () => import('@/view/directive/directive.vue')
            }
        ]
    },
    {
        path: '/multilevel',
        name: 'multilevel',
        meta: {
            icon: 'arrow-graph-up-right',
            title: ''
        },
        component: Main,
        children: [
            {
                path: 'level_2_1',
                name: 'level_2_1',
                meta: {
                    icon: 'arrow-graph-up-right',
                    title: '-1'
                },
                component: () => import('@/view/multilevel/level-1.vue')
            },
            {
                path: 'level_2_2',
                name: 'level_2_2',
                meta: {
                    access: ['super_admin'],
                    icon: 'arrow-graph-up-right',
                    title: '-2'
                },
                component: parentView,
                children: [
                    {
                        path: 'level_2_2_1',
                        name: 'level_2_2_1',
                        meta: {
                            icon: 'arrow-graph-up-right',
                            title: ''
                        },
                        component: () => import('@/view/multilevel/level-2/level-2-1.vue')
                    }
                ]
            },
            {
                path: 'level_2_3',
                name: 'level_2_3',
                meta: {
                    icon: 'arrow-graph-up-right',
                    title: '-3'
                },
                component: parentView,
                children: [
                    {
                        path: 'level_2_3_1',
                        name: 'level_2_3_1',
                        meta: {
                            access: ['super_admin'],
                            icon: 'arrow-graph-up-right',
                            title: '-1'
                        },
                        component: () => import('@/view/multilevel/level-2/level-2-1.vue')
                    },
                    {
                        path: 'level_2_3_2',
                        name: 'level_2_3_2',
                        meta: {
                            access: ['super_admin', 'admin'],
                            icon: 'arrow-graph-up-right',
                            title: '-2'
                        },
                        component: () => import('@/view/multilevel/level-2/level-2-1.vue')
                    }
                ]
            }
        ]
    },
    {
        path: '/401',
        name: 'error_401',
        component: () => import('@/view/error-page/401.vue')
    },
    {
        path: '/500',
        name: 'error_500',
        component: () => import('@/view/error-page/500.vue')
    },
    {
        path: '*',
        name: 'error_404',
        component: () => import('@/view/error-page/404.vue')
    }
];
