import Cookies from 'js-cookie'
// cookie
import config from '@/config'
import {forEach, hasOneOf} from '@/libs/tools'

export const TOKEN_KEY = 'token'

export const setToken = (token) => {
    Cookies.set(TOKEN_KEY, token, {expires: config.cookieExpires || 1})
}

export const getToken = () => {
    const token = Cookies.get(TOKEN_KEY)
    if (token) return token
    else return false
}

export const hasChild = (item) => {
    return item.children && item.children.length !== 0
}

const showThisMenuEle = (item, access) => {
    if (item.meta && item.meta.access && item.meta.access.length) {
        if (hasOneOf(item.meta.access, access)) return true
        else return false
    } else return true
}
/**
 * @param {Array} list 
 * @returns {Array}
 */
export const getMenuByRouter = (list, access) => {
    let res = []
    forEach(list, item => {
        if (item.meta && !item.meta.hideInMenu) {
            let obj = {
                icon: (item.meta && item.meta.icon) || '',
                name: item.name,
                meta: item.meta
            }
            if (hasChild(item) && showThisMenuEle(item, access)) {
                obj.children = getMenuByRouter(item.children, access)
            }
            if (showThisMenuEle(item, access)) res.push(obj)
        }
    })
    return res
}

/**
 * @param {Array} routeMetched metched
 * @returns {Array}
 */
export const getBreadCrumbList = (routeMetched) => {
    let res = routeMetched.filter(item => {
        return item.meta === undefined || !item.meta.hide
    }).map(item => {
        let obj = {
            icon: (item.meta && item.meta.icon) || '',
            name: item.name,
            meta: item.meta
        }
        return obj
    })
    res = res.filter(item => {
        return !item.meta.hideInMenu
    })
    return [{
        name: 'home',
        to: '/home'
    }, ...res]
}

export const showTitle = (item, vm) => vm.$config.useI18n ? vm.$t(item.name) : ((item.meta && item.meta.title) || item.name)

/**
 * @description 
 */
export const setTagNavListInLocalstorage = list => {
    localStorage.tagNaveList = JSON.stringify(list)
}
/**
 * @returns {Array} name, path, meta
 */
export const getTagNavListFromLocalstorage = () => {
    const list = localStorage.tagNaveList
    return list ? JSON.parse(list) : []
}

/**
 * @param {Array} routers 
 * @description namehome
 */
export const getHomeRoute = routers => {
    let i = -1
    let len = routers.length
    let homeRoute = {}
    while (++i < len) {
        let item = routers[i]
        if (item.children && item.children.length) {
            let res = getHomeRoute(item.children)
            if (res.name) return res
        } else {
            if (item.name === 'home') homeRoute = item
        }
    }
    return homeRoute
}

/**
 * @param {*} list 
 * @param {*} newRoute 
 * @description newRoute
 */
export const getNewTagList = (list, newRoute) => {
    const {name, path, meta} = newRoute
    let newList = [...list]
    if (newList.findIndex(item => item.name === name) >= 0) return newList
    else newList.push({name, path, meta})
    return newList
}

/**
 * @param {Boolean} status  1 => locked  0 => unlocked
 * @description 
 */
export const setLockStatus = (status) => {
    localStorage.isLocked = status
}
export const getLockStatus = () => {
    return parseInt(localStorage.isLocked)
}

/**
 * @param {*} access  ['super_admin', 'admin']
 * @param {*} route 
 */
const hasAccess = (access, route) => {
    if (route.meta && route.meta.access) return hasOneOf(access, route.meta.access)
    else return true
}

/**
 * @param {*} name name
 * @param {*} access 
 * @param {*} routes 
 * @description 
 */
export const canTurnTo = (name, access, routes) => {
    const getHasAccessRouteNames = (list) => {
        let res = []
        list.forEach(item => {
            if (item.children && item.children.length) {
                res = [].concat(res, getHasAccessRouteNames(item.children))
            } else {
                if (item.meta && item.meta.access) {
                    if (hasAccess(access, item)) res.push(item.name)
                } else {
                    res.push(item.name)
                }
            }
        })
        return res
    }
    const canTurnToNames = getHasAccessRouteNames(routes)
    return canTurnToNames.indexOf(name) > -1
}

/**
 * @param {String} url
 * @description URL
 */
export const getParams = url => {
    const keyValueArr = url.split('?')[1].split('&')
    let paramObj = {}
    keyValueArr.forEach(item => {
        const keyValue = item.split('=')
        paramObj[keyValue[0]] = keyValue[1]
    })
    return paramObj
}

/**
 * @param {Array} list 
 * @param {String} name name
 */
export const getNextName = (list, name) => {
    let res = ''
    if (list.length === 2) {
        res = 'home'
    } else {
        if (list.findIndex(item => item.name === name) === list.length - 1) res = list[list.length - 2].name
        else res = list[list.findIndex(item => item.name === name) + 1].name
    }
    return res
}

/**
 * @param {Number} times 
 * @param {Function} callback 
 */
export const doCustomTimes = (times, callback) => {
    let i = -1
    while (++i < times) {
        callback()
    }
}

/**
 * @param {Object} file 
 * @returns {Promise} resolve
 * @description Csv
 */
export const getArrayFromFile = (file) => {
    let nameSplit = file.name.split('.')
    let format = nameSplit[nameSplit.length - 1]
    return new Promise((resolve, reject) => {
        let reader = new FileReader()
        reader.readAsText(file) // 
        let arr = []
        reader.onload = function (evt) {
            let data = evt.target.result // 
            let pasteData = data.trim()
            arr = pasteData.split((/[\n\u0085\u2028\u2029]|\r\n?/g)).map(row => {
                return row.split('\t')
            }).map(item => {
                return item[0].split(',')
            })
            if (format === 'csv') resolve(arr)
            else reject(new Error('[Format Error]:Csv'))
        }
    })
}

/**
 * @param {Array} array 
 * @returns {Object} { columns, tableData }
 * @description iView
 */
export const getTableDataFromArray = (array) => {
    let columns = []
    let tableData = []
    if (array.length > 1) {
        let titles = array.shift()
        columns = titles.map(item => {
            return {
                title: item,
                key: item
            }
        })
        tableData = array.map(item => {
            let res = {}
            item.forEach((col, i) => {
                res[titles[i]] = col
            })
            return res
        })
    }
    return {
        columns,
        tableData
    }
}
